.section {
  margin: 0 10rem; }

.section-heading {
  font-size: 1.5rem;
  padding-left: 0.5rem;
  border-left: 3px solid #b0003a;
  width: fit-content; }

.section-content {
  display: flex; }
  .section-content .section-children {
    margin: 0 3rem; }
  .section-content .section-img-container {
    margin: auto; }
  .section-content .section-img {
    width: 20rem;
    max-height: 25rem; }

button {
  display: block;
  margin: 1rem;
  border-radius: 10px;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  box-shadow: 0px 2px 8px 2px rgba(0, 0, 0, 0.13); }

.read-more {
  color: white;
  background: #b0003a;
  border-color: #970032; }

.read-less {
  color: #b0003a;
  background: #28404d;
  border-color: #1f323c; }

button:focus {
  outline: 0; }

@media screen and (max-width: 1024px) {
  .section {
    margin: 0 3rem; } }

@media screen and (max-width: 425px) {
  .section {
    margin: 0; }
  .section-content {
    flex-direction: column; } }
