@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
/* TODO Think about this */
::-webkit-scrollbar {
  display: none; }

* {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  color: #28404d;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

button:focus {
  outline: 0; }

a {
  color: #b0003a; }

a:hover {
  color: #b0003a; }

html,
body {
  margin: 0;
  height: 100%; }

#__next {
  height: 100%;
  min-height: 100%; }

.site {
  overflow: hidden;
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 100%; }

.logo {
  display: block;
  margin: auto;
  width: 30rem; }

.slogan {
  display: block;
  margin: 2rem auto 0;
  width: fit-content;
  text-align: center; }

hr {
  border: none;
  background: #dbdbdb;
  height: 1px;
  margin: 3rem 0; }

.get-started {
  display: block;
  margin: 2rem auto 2rem auto;
  color: white;
  background: #b0003a;
  border-color: #970032;
  border-radius: 10px;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  box-shadow: 0px 2px 8px 2px rgba(0, 0, 0, 0.13); }

.get-started-link {
  border: none;
  text-decoration: none; }

.highlight-text {
  color: #b0003a; }

@media screen and (max-width: 425px) {
  .logo {
    width: 20rem; } }
