.description-container {
  display: flex; }

.active-button {
  color: white;
  background: #b0003a;
  border-color: #970032; }

.highlight-text {
  color: #b0003a; }

.code-img {
  max-width: 35rem; }

@media screen and (max-width: 768px) {
  .description-container {
    flex-direction: column; } }

@media screen and (max-width: 425px) {
  .code-img {
    max-width: 18rem; } }
